/* Error Tool Tip Styles*/
.form-item--error-message {
  position: relative;
  top: 100%;
  left: -1px;
  margin-top: -1px;
  z-index: 101;
  width: fit-content;
  max-width: 56rem;

  &.error,
  .error {
    @include responsive-typo-map($normal-text);
    font-weight: bold;
    display: block;
    position: absolute;
    position: relative;
    padding: .9rem;
    color: $white;
    background-color: $hc-red;
    border: 1px solid $hc-red;
    border-radius: 3px;
    box-shadow: 4px 4px 9px 0 rgba(0, 0, 0, .3);
    opacity: 1;
    margin-top: 1rem;

    &::after {
      content: '';
      border: .9rem solid transparent;
      border-bottom-color: $hc-red;
      position: absolute;
      top: -1.8rem;
      left: .9rem;
    }
  }
}

.search-form {
  .form-item--error-message .error {
    margin-bottom: 0;
  }
}

.webform-submission-form input[type="submit"] {
  background: $link none;
  min-width: 13rem;
  @include adjust-font-size-to(18px);
  line-height: 1.5;
  border: 3px solid transparent;
  color: $white;
  font-weight: normal;
  text-shadow: none;
  text-underline-offset: .25em;
  box-shadow: none;
  margin: 1rem 0 0;
  text-align: center;
  padding: .6rem 1.6rem;
  cursor: pointer;
  border-radius: 0;

  &:hover {
    background: $ash-grey;
    outline: none;
    opacity: 1;
  }

  &:focus {
    background: $black;
  }
}
